<template>
  <el-form
    :model="loginForm"
    :rules="fieldRules"
    ref="loginForm"
    label-position="left"
    label-width="0px"
    class="demo-ruleForm login-container"
  >
    <h3 class="title">就业信息系统后台登录</h3>
    <el-form-item prop="adminId">
      <el-input type="text" v-model="loginForm.adminId" auto-complete="off" placeholder="管理员账号"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input
        type="password"
        v-model="loginForm.password"
        auto-complete="off"
        placeholder="密码"
        show-password
      ></el-input>
    </el-form-item>
    <el-form-item style="width:100%;">
      <el-button
        type="primary"
        style="width:48%;"
        @click.native.prevent="loginSubmit"
        :loading="logining"
      >登 录</el-button>
    </el-form-item>
    <el-form-item>
      <span class="registed">
        未注册，前往
        <router-link to="/registe">注册</router-link>
      </span>
    </el-form-item>
  </el-form>
</template>

<script>
import { mapMutations } from "vuex";
import Cookies from "js-cookie";
import { interAdminInfo } from "../../../../utils/employment.url";
import { getCookie, setCookie } from "../../../../utils/utils.js";
import { aesEncrypt, aesDecrypt } from "@/utils/apiUtil";
var crypto = require("crypto");
export default {
  name: "Login",
  data() {
    return {
      logining: false,
      loginForm: {
        adminId: "",
        password: ""
      },
      fieldRules: {
        adminId: [
          { required: true, message: "请输入学号/教工号", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      },
      checked: true
    };
  },
  methods: {
    ...mapMutations(["changeLogin"]),
    loginSubmit() {
      var key = "ILOVEKUANGMINYI";
      var password = aesEncrypt(this.loginForm.password, key);
      interAdminInfo(
        {
          adminId: this.loginForm.adminId,
          password: password
        },
        "post"
      )
        .then(res => {
          if (res.status == 200) {
            sessionStorage.setItem("adminInfo", JSON.stringify(res.data));
            window.location.href = `${process.env.BASE_URL}manageStage.html#/managehomePage`; //
            this.$message({
              message: res.message,
              type: "success"
            });
          } else {
            this.$message({
              message: res.message,
              type: "error"
            });
          }
        })
        .catch(function(res) {
          alert(res);
        });
    }
  },
  created() {
    // setCookie('access-token', "iusudfuic");
  }
};
</script>
<style lang="scss" scoped>
.login-container {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 170px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  text-align: center;
  .title {
    font-size: 18px;
    font-weight: 600;
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
  .el-radio {
    margin-left: 10px;
  }
  .registed {
    a {
      color: #ff6f6f;
    }
  }
}
</style>